<template>
  <div>
    <!-- <mrc_ess_chooseStaff v-model="pageName"></mrc_ess_chooseStaff>---- -->
    <mrc_ess_table v-if="$route.name=='staffPropertyGroup'"
                   :pageName='pageName'
                   :headers='headers'
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :isLoading='loading'
                   :items='desserts'>
      <template v-slot:top>
        <v-card-title style="height:60px;position: relative;">
          <mrc_ess_searchInput style="position:absolute;right:10px;top:18px;"></mrc_ess_searchInput>
          <!-- <mrc_ess_chooseDepartment v-model="pageName"></mrc_ess_chooseDepartment> -->
        </v-card-title>
      </template>

      <template v-slot:item.actions>
        <v-icon small
                class="mr-2"
                @click="godetail">
          mdi-pencil
        </v-icon>
      </template>
    </mrc_ess_table>
    <template v-else>
      <router-view></router-view>
    </template>
  </div>
</template>

<script>
import { getStaffPropertyGroup } from '@/service/api/staffmanagement.js'
export default {
  components: {
  },
  props: {},
  data () {
    return {
      loading: false,
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      pageName: 'addstaffPropertyGroup',
      desserts: []
    };
  },
  created () { },
  async mounted () {
    await this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    goadd () {
      this.$store.commit('set_uuid', '')
      this.$store.commit('set_subPage_name', '')
      this.$router.push({
        name: 'addstaffPropertyGroup'
      })
    },
    rowgodetail (item) {
      this.$store.commit('set_iconstatus', true)
      this.$store.commit('set_uuid', item.uuid)
      this.$store.commit('set_subPage_name', item.staff_property_group_name)
      this.$router.push({
        name: 'detailstaffPropertyGroup',
        params: {
          uuid: item.uuid,
        }
      })
    },
    godetail (item) {
      this.$store.commit('set_iconstatus', false)
      this.$store.commit('set_uuid', item.uuid)
      this.$store.commit('set_subPage_name', item.staff_property_group_name)
      this.$router.push({
        name: 'detailstaffPropertyGroup',
        params: {
          uuid: item.uuid,
        }
      })
    },
    async getlist (obj) {  //获取列表
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getStaffPropertyGroup({
        cpage: this.options.cpage,
        pagesize: this.options.pagesize
      })
      if (res.code == 200) {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    }
  },
  computed: {
    headers () {
      return [
        {
          text: 'Staff Property Group',
          align: 'start',
          sortable: false,
          value: 'staff_property_group_name',
          width: '600'
        },
        {
          text: '',
          value: 'actions',
          sortable: false,
          align: 'end',
        }
      ]
    }
  },
  watch: {
    $route (v) {
      if (v.name == 'staffPropertyGroup') {
        this.getlist()
      }
    }
  },
};
</script>

